<template>
  <table>
    <thead>
      <tr>
        <th>Username</th>
        <th>Email</th>
        <th>Name &amp; Surname</th>
        <th>Select</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(consumer, index) in consumers" v-bind:key="index">
        <td v-if="index !== consumers.length - 1">
          {{ consumer.username }}
        </td>
        <td v-else>
          <input
            type="text"
            v-model="consumer.username"
            @keyup="[checkUsername(consumer.username), checkLastCheckbox()]"
            @keydown.enter.prevent
            @change="checkLastCheckbox()"
          />
        </td>
        <td v-if="index !== consumers.length - 1">
          {{ consumer.email }}
        </td>
        <td v-else>
          <input
            type="text"
            v-model="consumer.email"
            @keydown.enter.prevent
            @keyup="checkLastCheckbox()"
          />
        </td>
        <td v-if="index !== consumers.length - 1">
          {{ consumer.nameSurname }}
        </td>
        <td v-else>
          <input
            type="text"
            v-model="consumer.nameSurname"
            @keydown.enter.prevent
            @keyup="checkLastCheckbox()"
          />
        </td>
        <td v-if="index !== consumers.length - 1">
          <input
            type="checkbox"
            v-bind:name="consumer.username"
            v-model="consumer.checked"
            v-bind:value="consumer.username"
          />
        </td>
        <td v-else>
          <input
            type="checkbox"
            v-bind:name="consumer.username"
            v-model="consumer.checked"
            v-bind:value="consumer.username"
            disabled
          />
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
export default {
  name: "ConsumerSendTable",
  props: ["consumers"],
  methods: {
    checkUsername: function (username) {
      this.consumers[
        this.consumers.length - 1
      ].username = username.toUpperCase();
      let regex = /^[A-Z]{6}[0-9]{2}[ABCDEHLMPRST]{1}[0-9]{2}([A-Z]{1}[0-9]{3})[A-Z]{1}$/i;
      if (regex.exec(username)) {
        document.querySelector("input[type='submit']").disabled = false;
      } else {
        document.querySelector("input[type='submit']").disabled = true;
      }
    },

    checkLastCheckbox: function () {
      let newConsumer = this.consumers[this.consumers.length - 1];
      if (
        newConsumer.username !== "" &&
        newConsumer.email !== "" &&
        newConsumer.nameSurname !== ""
      ) {
        document.querySelector(
          "tr:last-child td input[type='checkbox']"
        ).disabled = false;
      } else {
        document.querySelector(
          "tr:last-child td input[type='checkbox']"
        ).disabled = true;
        document.querySelector(
          "tr:last-child td input[type='checkbox']"
        ).checked = false;
      }
    },
  },
};
</script>

